import { Injectable } from "@angular/core";
import { HttpClient,HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs";
const httpOptions = {
    headers: new HttpHeaders({
      "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
    })
  };
@Injectable()
export class HttpService{
    //依赖注入
    constructor(public http:HttpClient){}
    // 费率api地址
    baseUrl = "http://192.168.0.129:8885/parkingmeter/v1/rate/"
    // 道路和区域api地址
    roadUrl = "http://192.168.0.129:8885/parkingmeter/v1/common/tag/search-tag";
    // 泊位列表api
    supplierApi = "http://192.168.0.129:8885/parkingmeter/v1/parkplace/provider/list?provinceId=430&cityId=60&countyId=1656";
    // 日期类型api
    calendarApi =
    "http://192.168.0.129:8881/parkingmeter/v1/calendar/datetype?regionId=1";
    // 自定义日期标签api
    customApi =
    "http://192.168.0.129:8881/parkingmeter/v1/calendar/label?regionId=1";
    // 获取费率列表
    getRateList():Observable<any> {
        return this.http.get(this.baseUrl+"list?regionId=1")
    }
    // 新增费率
    addRate(params):Observable<any> {
        return this.http.post(this.baseUrl+"new", params, httpOptions)
    }
    // 修改费率
    editorRate(rateid,params){
        return this.http.put(this.baseUrl+rateid,params,httpOptions)
    }
    // 获取费率详情
    getRateInfo(rateid):Observable<any> {
        return this.http.get(this.baseUrl+rateid+"/info")
    }
    // 新增执行计划
    addSchedule(rateid,params):Observable<any> {
        return this.http.post(this.baseUrl+rateid +"/schedule", params, httpOptions)
    }
    // 修改未启用执行计划
    editorSchedule(rateid,scheduleId,params):Observable<any> {
        return this.http.put(this.baseUrl+rateid+"/schedule/"+scheduleId,params,httpOptions)
    }
    // 删除未启用执行计划
    deleteSchedule(scheduleId):Observable<any> {
        return this.http.delete(this.baseUrl+"schedule/"+scheduleId +"?operator=10", httpOptions)
    }
    // 获取道路级别
    getRoadLevel(params):Observable<any> {
        return this.http.post(this.roadUrl,params,httpOptions)
    }
    // 获取区域级别
    getAreaLevel(params):Observable<any> {
        return this.http.post(this.roadUrl,params,httpOptions)
    }
    // 获取泊位提供商
    getSupplier():Observable<any> {
        return this.http.get(this.supplierApi)
    }
    // 获取日期类型
    getCalendarType():Observable<any> {
        return this.http.get(this.calendarApi)
    }
    // 获取自定义日期标签
    getCustomCalendar():Observable<any>{
        return this.http.get(this.customApi)
    }
}
